// ===============================
// src/types/index.ts
// ===============================
export interface Purchase {
  itemName: string;
  cost: number;
}

export interface Job {
  name: string;
  salary: number;
}

export interface AgentState {
  id: number;
  name: string;
  personality: string;
  memory: string;
  currentAction: string;
  energy: number;
  mood: MoodType;
  happiness: number; // 
  hunger: number;    // 
  location: Location; // 
  currentLocationName: string; // 
  goals: string[];
  shortTermPlan: string; // 
  relationships: Record<string, number>; // agent_id -> relationship_strength
  receivedMessages: Message[]; // 
  summarizedMemory: string; // 
  money: number; // 
  job: Job | null; // 
  weapon: Weapon | null; // 
  inventory: Record<string, number>; //  ( -> )
}

export interface Message {
  senderId: number;
  recipientId: number; // 
  content: string;
  timestamp: string;
}

export interface ActionHistory {
  action: string;
  timestamp: string;
  step: number;
  context?: string;
}

export interface Location {
  x: number;
  y: number;
}

export interface NamedLocation extends Location {
  name: string;
  type: 'home' | 'supermarket' | 'general_store' | 'park' | 'work' | 'library' | 'cafe' | 'base' | 'other'; // 
  width: number; // 
  height: number; // 
  ownerAgentId?: number; // ID
  resources?: string[]; //  (: '', '')
  health?: number; // 
}

export const MoodTypes = [
  'happy',
  'neutral',
  'excited',
  'thoughtful',
  'content',
  'tired',
  'social',
  'creative',
] as const;

export type MoodType = typeof MoodTypes[number];

export interface SimulationLog {
  type: 'system' | 'action' | 'error' | 'info' | 'debug';
  message: string;
  timestamp: string;
  agentId?: number;
}

export type LLMProvider = 'huggingface' | 'openai' | 'anthropic' | 'ollama';

export interface LLMConfig {
  provider: LLMProvider;
  model: string;
  temperature: number;
  maxTokens: number;
  apiKey: string;
  baseUrl?: string;
}

export interface LLMResponse {
  action: string;
  reasoning?: string;
  mood?: MoodType;
  energy?: number;
  happiness?: number; // 
  hunger?: number;    // 
}

export interface ActionResponse extends LLMResponse {
  plan?: string; // 
  isMessage?: boolean;
  recipientName?: string;
  messageContent?: string;
  targetLocation?: string; 
  purchase?: Purchase; // 
}

export interface Zombie {
  id: number;
  x: number;
  y: number;
  health: number;
  targetAgentId: number | null; // ID
}

export interface Weapon {
  name: string;
  damage: number;
  range: number; // 
  type: 'melee' | 'ranged';
}
