import { ActionHistory } from '../types';
import { LLMService } from './llm'; // LLMService

export class MemoryManager {
  private actionHistory: ActionHistory[] = [];
  private summarizedMemories: string[] = [];

  addMemory(action: string, timestamp: string, step: number, context: string): void {
    const actionEntry: ActionHistory = {
      action,
      timestamp,
      step,
      context,
    };
    this.actionHistory.push(actionEntry);
  }

  async summarizeMemories(llmService: LLMService): Promise<void> {
    if (this.actionHistory.length === 0) {
      return;
    }

    const historyToSummarize = this.actionHistory.map(h => `[${h.step}] ${h.timestamp}: ${h.action}`).join('\n');
    const summary = await llmService.summarizeText(historyToSummarize);
    this.summarizedMemories.push(summary);
    this.actionHistory = []; // 
    console.log('Memories summarized:', summary);
  }

  getMemoryContext(): string {
    let context = this.summarizedMemories.join('\n\n[]\n');
    if (this.actionHistory.length > 0) {
      context += (context ? '\n\n[]\n' : '') + this.actionHistory.map(h => `[${h.step}] ${h.timestamp}: ${h.action}`).join('\n');
    }
    return context || "";
  }

  async retrieveRelevantMemories(currentContext: string, llmService: LLMService): Promise<string> {
    const allMemories = this.summarizedMemories.concat(this.actionHistory.map(h => `[${h.step}] ${h.timestamp}: ${h.action}`));
    if (allMemories.length === 0) {
      return "";
    }

    const prompt = `:\n${currentContext}\n\n\n\n${allMemories.join('\n')}\n\n:`;

    try {
      const relevantMemory = await llmService.summarizeText(prompt);
      return relevantMemory;
    } catch (error) {
      console.error('Failed to retrieve relevant memories:', error);
      return "";
    }
  }
}
