import { Agent } from '../models/Agent';
import { ActionResponse, NamedLocation } from '../types';

export class RuleEngine {
  decideAction(agent: Agent, allAgents: Agent[], availableLocations: NamedLocation[], currentStep: number): ActionResponse | null {
    // : 20%
    if (agent.energy <= 20) {
      return {
        action: "",
        mood: 'tired',
        energy: 10 // 
      } as ActionResponse;
    }

    // : 
    if (agent.goals.includes("") && agent.currentAction !== "") {
      return {
        action: "",
        mood: 'neutral',
        energy: -5
      } as ActionResponse;
    }

    // 
    // LLM
    if (agent.currentAction.includes('')) {
      const moveRegex = /^\[(.*?)\]/;
      const moveMatch = agent.currentAction.match(moveRegex);
      if (moveMatch) {
        const targetLocationName = moveMatch[1];
        const targetLocation = availableLocations.find(l => l.name === targetLocationName);

        if (targetLocation && targetLocation.ownerAgentId) {
          // 
          const ownerAgent = allAgents.find(a => a.id === targetLocation.ownerAgentId);
          if (ownerAgent) {
            const relationshipStrength = agent.relationships[ownerAgent.id.toString()] || 0;
            // : 20
            if (relationshipStrength < 20) {
              return {
                action: `${ownerAgent.name}`, // 
                mood: 'neutral',
                energy: -1,
                reasoning: 'Rule: Private property access denied due to low relationship.'
              } as ActionResponse;
            }
          }
        }
      }
    }

    // : 
    // : 5040-49
    const isNightTime = currentStep % 50 >= 40;
    const noisyActions = ["", "", "", ""]; // 

    if (isNightTime && noisyActions.some(keyword => agent.currentAction.includes(keyword))) {
      return {
        action: "",
        mood: 'thoughtful', // 
        energy: -2, // 
        happiness: -5, // 
        reasoning: 'Rule: Noise prohibited during night time.'
      } as ActionResponse;
    }

    // ...

    return null; // null
  }
}
